.booking {
  min-height: 555px;

  background-image: url("../img/background/booking-background.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-color: #b6d9fa;

  @include vp-1023 {
    background-image: url("../img/background/booking-tablet.png");
  }

  @include vp-767 {
    background-image: url("../img/background/booking-mobile.png");
  }

  @include retina {
    background-image: url("../img/background/booking-background@2x.png");

    @include vp-1023 {
      background-image: url("../img/background/booking-tablet@2x.png");
    }

    @include vp-767 {
      background-image: url("../img/background/booking-mobile@2x.png");
    }
  }
}

.booking .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 50px 163px;

  @include vp-1023 {
    flex-direction: column;
    padding-top: 100px;
    padding-bottom: 100px;
  }

  @include vp-767 {
    padding: 65px 15px;
  }
}

.booking h2 {
  font-size: 40px;
  line-height: 60px;
  margin: 0;
  margin-bottom: 30px;

  @include vp-1023 {
    font-size: 34px;
    line-height: 51px;
    margin-bottom: 25px;
  }

  @include vp-767 {
    text-align: center;
  }
}

.booking p {
  margin: 0;
  font-size: 18px;
  line-height: 27px;

  @include vp-1023 {
    text-align: center;
  }

  @include vp-767 {
    font-size: 16px;
    line-height: 22px;
  }
}

.booking__content {
  margin-right: 20px;
  //change
  max-width: 50%;

  @include vp-1023 {
    max-width: 100%;
    margin-right: 0;
    margin-bottom: 60px;
  }

  @include vp-767 {
    margin-bottom: 40px;
  }
}

.booking__form {
  display: flex;
  flex-direction: column;
  position: relative;
  min-height: 455px;
  max-width: 403px;
  padding: 50px;
  background-color: $color-dark;
  color: $color-light;
  box-shadow: 0 4px 45px rgba(1, 28, 64, 0.15);

  @include vp-767 {
    padding: 40px 30px;
  }
}

.booking__form p {
  font-weight: 500;
  margin-bottom: 35px;

  @include vp-767 {
    font-size: 18px;
    line-height: 25px;
  }
}

.booking__form input {
  width: 100%;
  height: 43px;
  margin-bottom: 20px;
  padding: 11px 16px;
  border: none;
  background-color: $color-light;

  &::placeholder {
    font-size: 14px;
    line-height: 21px;
    color: rgba($color: $color-dark, $alpha: 0.7);
  }

  @include hover {
    outline: 2px solid $color-light;
    outline-offset: 1px;
  }

  @include focus {
    outline: 2px solid $color-light;
    outline-offset: 2px;
  }
}

.booking__form input[type="checkbox"] {
  width: 303px;
  bottom: 120px;
  margin-bottom: 0;

  @include vp-767 {
    width: 230px;
  }

  @include hover {
    + label::before {
      outline: 2px solid $color-light;
      outline-offset: 1px;
    }
  }

  @include focus {
    +label::before {
      outline: 2px solid $color-light;
      outline-offset: 2px;
    }
  }
}

.booking__form input[type="checkbox"] + label {
  font-size: 10px;
  line-height: 13px;
  color: $color-light;
  padding-left: 23px;
  position: relative;
  cursor: pointer;
  user-select: none;
  margin-bottom: 30px;

  &::before {
    content: "";
    position: absolute;
    width: 18px;
    height: 18px;
    left: 0;
    top: -2px;
    background-color: $color-light;
  }
}

.booking__form input[type="checkbox"]:checked + label::before {
  background-image: url("../img/svg/checked.svg");
  background-repeat: no-repeat;
  background-position: 4px 6px;
}

.booking__button {
  @include hover-focus {
    outline: 2px solid $color-light;
    outline-offset: 2px;
  }

  @include active {
    text-decoration: underline $color-dark;
  }
}
